:root {
  --primary: #4d5ae5;
  --pressed-state: #404bbf;
  --text: #434455;
  --white: #fff;
  --transition-duration: 250ms;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--white);
  color: var(--text);
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header {
  height: 55px;
  background-color: #3f56b3;
}

.header .container {
  height: 100%;
}

.search-form {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form input {
  height: 40px;
  float: left;
  width: 25%;
  border: 1px solid gray;
  border-right: none;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 17px;
}

.search-form input:focus {
  outline: none;
}

.search-form button {
  height: 40px;
  float: left;
  width: 4%;
  color: #fff;
  cursor: pointer;
  background: #f0f0f0;
  border: 1px solid gray;
  border-left: none;
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.search-form button:hover, .search-form button:focus {
  border-color: none;
  background-color: #a0b5ea;
}

.container {
  width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 32px;
  padding-right: 32px;
}

.section {
  padding-top: 20px;
  padding-bottom: 20px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  display: grid;
}

.gallery-item {
  box-shadow: 0 2px 5px -1px rgba(50, 50, 93, .25), 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.info-item {
  text-align: center;
}

.gallery-item .info {
  min-height: 70px;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.photo-card {
  width: 100%;
}

.img-thumb {
  width: 100%;
  height: 180px;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.card-img {
  width: 100%;
}

.button {
  background-color: var(--primary);
  color: var(--white);
  letter-spacing: .04em;
  cursor: pointer;
  transition: background-color var(--transition-duration) var(--transition-timing-function);
  border: none;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 16px;
  font-weight: 500;
  line-height: 1.5;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button:hover {
  background-color: var(--pressed-state);
}

.button.is-hidden {
  display: none;
}

